  display: flex;
  margin-top:-100px !important;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  -webkit-animation: fadeIn 3s ;
          animation: fadeIn 3s ;
}

.header h1 {
  font-size: 3rem;
  margin-bottom: 20px;
  font-family: 'LT Neo Rounded', sans-serif;
                                                
}

.about-us {
  padding: 50px;
  text-align: center;
  -webkit-animation: slideUp 2s ease-in-out;
          animation: slideUp 2s ease-in-out;
}

.about-us h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}

/* Animations */
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes slideUp {
  from {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
    opacity: 0;
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
    opacity: 0;
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}
